
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapState, mapMutations, mapActions } from 'vuex'

export default {
components: {
ViewBox,
},
data: function () {
return {
}
},
computed: {
...mapState('settings', ['language', 'configIndex']),
...mapState('ai', ['messages']),
route() {
return this.$route
},
},
methods: {
...mapMutations('settings', ['setValue']),
...mapActions('ai', ['initEngine']),
...mapActions('settings', ['readCookies']),
...mapActions(['getBrowserCapabilities']),
},
watch: {
language(newValue) {
this.$i18n.locale = newValue
},
},
created() {
this.getBrowserCapabilities()
this.readCookies()
if (!this.language) {
this.setValue({ key: 'language', value: this.$i18n.locale })
} else {
this.$i18n.locale = this.language
}
},
mounted() {



if (navigator.serviceWorker) {
navigator.serviceWorker.getRegistrations().then(registrations => {
for (let registration of registrations) {
if (registration.active && registration.active.scriptURL.indexOf('coi-serviceworker') === -1) {

console.log('Unregistering conflicting service worker:', registration.active.scriptURL)
registration.unregister()
}
}
})
}

const _this = this

this.$vux.alert.show_i18n = function (options) {
options.buttonText = _this.$t('common.ok')
_this.$vux.alert.show(options)
}
this.$vux.confirm.show_i18n = function (options) {
options.confirmText = _this.$t('common.confirm')
options.cancelText = _this.$t('common.cancel')
_this.$vux.confirm.show(options)
}
this.$vux.confirm.prompt_i18n = function (value, options) {
options.confirmText = _this.$t('common.confirm')
options.cancelText = _this.$t('common.cancel')
_this.$vux.confirm.prompt(value, options)
}

window.onresize = function () {

_this.$store.commit('setScreenSize', {
width: document.documentElement.clientWidth,
height: document.documentElement.clientHeight,
})
}

const loadEngine = (loadFullEngine) => {



if (!window.crossOriginIsolated && navigator.serviceWorker) {

console.warn('Cross-Origin Isolation is NOT active. Waiting for Service Worker to reload...')


const reloaded = window.sessionStorage.getItem("coiReloadedBySelf")


setTimeout(() => {
if (!window.crossOriginIsolated) {


_this.$vux.confirm.show_i18n({
title: _this.$t('game.loadingError'),
content: _this.$t('game.isolationFailed'),
onConfirm: () => {


window.sessionStorage.removeItem("coiReloadedBySelf")
navigator.serviceWorker.getRegistrations().then(registrations => {
const unregisterPromises = registrations.map(r => r.unregister())
Promise.all(unregisterPromises).then(() => {

console.log('Unregistered all SWs, reloading...')
window.location.reload()
})
})
}
})
}
}, reloaded ? 1000 : 4000)
return
}

_this.initEngine(loadFullEngine).catch((err) => {
_this.$vux.alert.show_i18n({
title: _this.$t('game.engineLoadingError'),
content: err.toString(),
})
})
}

loadEngine(true)
},
}
